export default {
	data() {
		return {};
	},
	props: {
		tabBar: Array,
		tabIndex: Number,
		scrollStyle: {
			type: String,
			default: ''
		},
		scrollItemStyle: {
			type: String,
			default: ''
		}
	},
	methods: {
		//子组件向父组件传递数据 index
		tabtap(index) {
			this.$emit('tabtap', index);
		}
	}
};
